import React from 'react';

const Card = ({ id, name, email }) => {
    return (
        <div className="bg-light-green br3 dib ma2 pa3 grow bw2 shadow-5 tc">
           <img src={`https://robohash.org/${id}?200*200`} alt='robot' />
           <div>
            <h2>{ name }</h2>
            <p>{ email }</p>
           </div>
        </div>
    );
}

export default Card;